import logo from './logo.svg';
import './App.css';
import React from 'react';

function App() {
  const [word, setWord] = React.useState((localStorage.getItem('word') || "Daha boş ;)"));
  const [count, setCount] = React.useState(Number(localStorage.getItem('count')|| 0))
  const clickHandler = event => {
    if(event.target.name == "button"){
      setWord(document.getElementById('input').value)
      setCount(count + 1)
    }
  };
  localStorage.setItem('word', word)
  localStorage.setItem('count',count)
  return (
    <div className='px-auto text-center mt-5' >
      <h3>{word}</h3>
      <input type="text" id='input' className='form-control mx-auto' style={{maxWidth:'10rem'}} />
      <a href="#" className='btn btn-primary mx-auto my-5' onClick={clickHandler} name='button' >Değiştir</a>
      <p>Yükarıdaki butona {count} kez bastığını biliyormusun?</p>
    </div>
  );
}


export default App;
